export interface PreviewData {
  imageSrc: string;
  width: number;
  height: number;
  sourceWidth: number;
  sourceHeight: number;
  proxyEnabled: boolean;
  proxyScale: number;
  proxyReason: string;
  averageDelayMs?: number | null;
  targetDelayMs?: number | null;
  generatedAt: string;
  profile: string;
}

interface PreviewPanelProps {
  preview: PreviewData | null;
  loading: boolean;
  error: string | null;
  onRefresh(): void;
  onImageRendered(preview: PreviewData): void;
  proxyMode: 'auto' | 'on' | 'off';
  onProxyModeChange(mode: 'auto' | 'on' | 'off'): void;
}

export function PreviewPanel({
  preview,
  loading,
  error,
  onRefresh,
  onImageRendered,
  proxyMode,
  onProxyModeChange
}: PreviewPanelProps) {
  const proxyReasonLabels: Record<string, string> = {
    client_force_on: ' ON',
    client_force_off: ' OFF',
    project_metadata_on: '',
    project_metadata_off: '',
    resolution_4k: ': 4K ',
    resolution_qhd: ': 1440p ',
    historical_delay: ': ',
    auto: ': ',
  };

  const reasonLabel = preview ? proxyReasonLabels[preview.proxyReason] ?? preview.proxyReason : '';

  return (
    <section className="app-shell__section preview-panel">
      <header className="preview-panel__header">
        <h2></h2>
        <div className="preview-panel__actions">
          <button type="button" onClick={() => onRefresh()} disabled={loading}>
            
          </button>
          <div className="preview-panel__proxy-toggle" role="group" aria-label="Proxy Mode">
            <button
              type="button"
              className={proxyMode === 'auto' ? 'is-active' : ''}
              onClick={() => onProxyModeChange('auto')}
              disabled={loading}
            >
              
            </button>
            <button
              type="button"
              className={proxyMode === 'on' ? 'is-active' : ''}
              onClick={() => onProxyModeChange('on')}
              disabled={loading}
            >
               ON
            </button>
            <button
              type="button"
              className={proxyMode === 'off' ? 'is-active' : ''}
              onClick={() => onProxyModeChange('off')}
              disabled={loading}
            >
               OFF
            </button>
          </div>
        </div>
      </header>

      {error ? <p className="preview-panel__error">: {error}</p> : null}

      <div className={`preview-panel__viewport${loading ? ' preview-panel__viewport--loading' : ''}`}>
        {preview ? (
          <img
            key={preview.generatedAt}
            src={preview.imageSrc}
            alt="NodeVision Preview"
            width={preview.width}
            height={preview.height}
            onLoad={() => onImageRendered(preview)}
            className="preview-panel__image"
          />
        ) : !loading ? (
          <p className="preview-panel__placeholder"></p>
        ) : null}
        {loading ? <div className="preview-panel__spinner"></div> : null}
      </div>

      {preview ? (
        <ul className="preview-panel__meta">
          <li>
            : {preview.sourceWidth}{preview.sourceHeight}
          </li>
          <li>
            : {preview.width}{preview.height}
          </li>
          <li>: {new Date(preview.generatedAt).toLocaleString('ja-JP')}</li>
          <li>
            : {preview.proxyEnabled ? '' : ''} {preview.proxyScale.toFixed(2)}x
          </li>
          <li>: {reasonLabel}</li>
          {preview.targetDelayMs != null ? (
            <li>: {preview.targetDelayMs.toFixed(0)} ms</li>
          ) : null}
          {preview.averageDelayMs != null ? (
            <li>: {preview.averageDelayMs.toFixed(1)} ms</li>
          ) : null}
          <li>: {preview.profile}</li>
        </ul>
      ) : null}
    </section>
  );
}

export default PreviewPanel;
